import React from 'preact/compat';
import { usePWAUpdate } from '../utils/usePWAUpdate';

export function PWAUpdateNotifier() {
  const { updateAvailable, isUpdating, installUpdate } = usePWAUpdate();

  const handleUpdate = async () => {
    await installUpdate();
  };

  const handleDismiss = () => {
    // For now, we'll just hide the notification
    // In a more advanced implementation, you could store this in localStorage
    // to prevent showing again for a certain period
  };

  if (!updateAvailable) return null;

  return (
    <div class="fixed bottom-4 left-4 right-4 md:left-auto md:right-4 md:max-w-sm z-50">
      <div class="bg-wgt-red text-white rounded-lg shadow-lg p-4 border border-gray-700">
        <div class="flex items-start space-x-3">
          <div class="flex-shrink-0">
            <svg class="w-6 h-6" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
            </svg>
          </div>
          <div class="flex-1">
            <h3 class="text-sm font-medium">Update Available!</h3>
            <p class="text-sm text-gray-100 mt-1">
              A new version of the WGT app is available. Update now to get the latest features and fixes.
            </p>
            <div class="mt-3 flex space-x-2">
              <button
                onClick={handleUpdate}
                disabled={isUpdating}
                class="bg-white text-wgt-red px-3 py-1 rounded text-sm font-medium hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {isUpdating ? 'Updating...' : 'Update Now'}
              </button>
              <button
                onClick={handleDismiss}
                class="bg-transparent border border-white text-white px-3 py-1 rounded text-sm hover:bg-white hover:text-wgt-red transition-colors"
              >
                Later
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 