import React, { useState, useEffect } from 'preact/compat';
import { Schedule as ScheduleType, EventWithDetails } from '../models/types';
import { loadScheduleData, isScheduleLoaded } from '../utils/apiUtils';
import { getAllEventsWithDetails, sortEventsByDateTime, loadFilters, saveFilters } from '../utils/dataUtils';
import { groupEventsByDate } from '../utils/scheduleUtils';
import { EventFilter, FilterOptions } from '../components/EventFilter';
import { DayGroup } from '../components/DayGroup';
import { LoadingSpinner } from '../components/LoadingSpinner';
import { ErrorMessage } from '../components/ErrorMessage';

export function Schedule() {
  const [schedule, setSchedule] = useState<ScheduleType | null>(null);
  const [events, setEvents] = useState<EventWithDetails[]>([]);
  const [filteredEvents, setFilteredEvents] = useState<EventWithDetails[]>([]);
  const [loading, setLoading] = useState(!isScheduleLoaded());
  const [error, setError] = useState<string | null>(null);
  const [uniqueDates, setUniqueDates] = useState<string[]>([]);
  const [expandedDays, setExpandedDays] = useState<Record<string, boolean>>({});
  const [savedFilters, setSavedFilters] = useState<FilterOptions | null>(null);
  
  // Load saved filters from localStorage on component mount
  useEffect(() => {
    const filters = loadFilters();
    setSavedFilters(filters);
  }, []);

  useEffect(() => {
    async function fetchData() {
      try {
        const data = await loadScheduleData();
        setSchedule(data);
        
        // Get all events with details
        const allEvents = getAllEventsWithDetails(data);
        
        // Sort events by date and time
        const sortedEvents = sortEventsByDateTime(allEvents);
        
        setEvents(sortedEvents);
        
        // Extract unique dates for the filter
        const dates = Array.from(new Set(sortedEvents.map(event => event.date)));
        setUniqueDates(dates);
        
        // Apply saved filters if available, otherwise show all events
        if (savedFilters) {
          applyFilters(sortedEvents, savedFilters);
        } else {
          setFilteredEvents(sortedEvents);
        }
      } catch (err) {
        setError('Failed to load schedule data. Please try again later.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
  }, [savedFilters]);

  const applyFilters = (eventsToFilter: EventWithDetails[], filters: FilterOptions) => {
    let filtered = [...eventsToFilter];
    
    // Filter by date
    if (filters.date) {
      filtered = filtered.filter(event => event.date === filters.date);
    }
    
    // Filter by location
    if (filters.locationId) {
      filtered = filtered.filter(event => event.locationId === filters.locationId);
    }
    
    // Filter by category
    if (filters.category) {
      filtered = filtered.filter(event => event.category === filters.category);
    }
    
    // Filter by search term (band name)
    if (filters.searchTerm) {
      const searchLower = filters.searchTerm.toLowerCase();
      filtered = filtered.filter(event => 
        event.band.name.toLowerCase().includes(searchLower) ||
        (event.band.genre && event.band.genre.toLowerCase().includes(searchLower))
      );
    }
    
    setFilteredEvents(filtered);
  };
  
  const handleFilterChange = (filters: FilterOptions) => {
    // Apply filters to events
    applyFilters(events, filters);
    
    // Save filters using dataUtils function
    saveFilters(filters);
  };

  const toggleDayExpanded = (date: string) => {
    setExpandedDays(prev => ({
      ...prev,
      [date]: prev[date] === undefined ? false : !prev[date]
    }));
  };

  const isDayExpanded = (date: string) => {
    return expandedDays[date] !== false; // Default to expanded
  };
  
  if (loading) {
    return <LoadingSpinner message="Loading schedule data..." />;
  }
  
  if (error) {
    return <ErrorMessage message={error} />;
  }
  
  return (
    <div>
      <h1 class="text-3xl font-bold mb-6">Festival Schedule</h1>
      
      {schedule && (
        <div class="sticky top-[100px] md:top-0 z-40 bg-gray-900 py-3 -mx-4 px-4 sm:-mx-6 sm:px-6 md:-mx-8 md:px-8">
          <EventFilter 
            onFilterChange={handleFilterChange} 
            locations={schedule.locations}
            dates={uniqueDates}
            initialFilters={savedFilters}
          />
        </div>
      )}
      
      {filteredEvents.length > 0 ? (
        <div>
          {Object.entries(groupEventsByDate(filteredEvents))
            .sort(([a], [b]) => a.localeCompare(b))
            .map(([date, dayEvents]) => (
              <DayGroup
                key={date}
                date={date}
                events={dayEvents}
                isExpanded={isDayExpanded(date)}
                onToggleExpanded={toggleDayExpanded}
              />
            ))}
        </div>
      ) : (
        <div class="card text-center py-8">
          <p class="text-xl text-gray-400">No events found matching your filters.</p>
          <p class="text-gray-500 mt-2">Try adjusting your filter criteria.</p>
        </div>
      )}
    </div>
  );
}