import React from 'preact/compat';
import { useState, useEffect } from 'preact/compat';

interface EventFilterProps {
  onFilterChange: (filters: FilterOptions) => void;
  locations: { id: string; name: string }[];
  dates: string[];
  initialFilters?: FilterOptions | null;
}

export interface FilterOptions {
  date: string;
  locationId: string;
  searchTerm: string;
  category: string;
}

export function EventFilter({ onFilterChange, locations, dates, initialFilters }: EventFilterProps) {
  const [filters, setFilters] = useState<FilterOptions>({
    date: '',
    locationId: '',
    searchTerm: '',
    category: ''
  });
  const [isExpanded, setIsExpanded] = useState(false);
  
  // Initialize filters with saved values when available
  useEffect(() => {
    if (initialFilters) {
      setFilters(initialFilters);
    }
  }, [initialFilters]);
  
  const handleFilterChange = (key: keyof FilterOptions, value: string) => {
    const newFilters = { ...filters, [key]: value };
    setFilters(newFilters);
    onFilterChange(newFilters);
  };
  
  const formatDate = (dateStr: string) => {
    if (!dateStr) return '';
    const date = new Date(dateStr);
    return date.toLocaleDateString('en-US', {
      weekday: 'short',
      month: 'short',
      day: 'numeric'
    });
  };

  const toggleExpanded = () => {
    setIsExpanded(!isExpanded);
  };

  // Check if any filters are active
  const hasActiveFilters = filters.date || filters.locationId || filters.searchTerm || filters.category;
  
  return (
    <div class="bg-gray-800 rounded-lg mb-6 overflow-hidden">
      {/* Header with toggle button */}
      <div 
        class="p-4 cursor-pointer flex items-center justify-between hover:bg-gray-700 transition-colors md:cursor-default md:hover:bg-gray-800"
        onClick={toggleExpanded}
      >
        <div class="flex items-center">
          <h2 class="text-xl font-semibold">Filter Events</h2>
          {hasActiveFilters && (
            <span class="ml-3 px-2 py-1 bg-wgt-red text-white text-xs rounded-full">
              Active
            </span>
          )}
        </div>
        <div class="ml-4 md:hidden">
          <svg 
            class={`w-6 h-6 transform transition-transform duration-300 ${isExpanded ? 'rotate-180' : ''}`}
            fill="none" 
            stroke="currentColor" 
            viewBox="0 0 24 24"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
        </div>
      </div>
      
      {/* Collapsible filter controls */}
      {/* On small screens: show only when expanded */}
      {/* On md+ screens: always show, ignore isExpanded state */}
      <div class={`px-4 pb-4 md:block ${isExpanded ? 'block' : 'hidden'}`}>
        <div class="grid grid-cols-1 md:grid-cols-4 gap-4">
          <div>
            <label class="block text-gray-400 mb-1" htmlFor="date-filter">
              Date
            </label>
            <select
              id="date-filter"
              class="w-full bg-gray-700 text-white rounded p-2 border border-gray-600"
              value={filters.date}
              onChange={(e) => handleFilterChange('date', (e.target as HTMLSelectElement).value)}
            >
              <option value="">All Dates</option>
              {dates.map(date => (
                <option key={date} value={date}>
                  {formatDate(date)}
                </option>
              ))}
            </select>
          </div>
          
          <div>
            <label class="block text-gray-400 mb-1" htmlFor="location-filter">
              Venue
            </label>
            <select
              id="location-filter"
              class="w-full bg-gray-700 text-white rounded p-2 border border-gray-600"
              value={filters.locationId}
              onChange={(e) => handleFilterChange('locationId', (e.target as HTMLSelectElement).value)}
            >
              <option value="">All Venues</option>
              {locations.map(location => (
                <option key={location.id} value={location.id}>
                  {location.name}
                </option>
              ))}
            </select>
          </div>
          
          <div>
            <label class="block text-gray-400 mb-1" htmlFor="category-filter">
              Category
            </label>
            <select
              id="category-filter"
              class="w-full bg-gray-700 text-white rounded p-2 border border-gray-600"
              value={filters.category}
              onChange={(e) => handleFilterChange('category', (e.target as HTMLSelectElement).value)}
            >
              <option value="">All Categories</option>
              <option value="band">Bands</option>
              <option value="show">Show Acts</option>
              <option value="other">Other Events</option>
            </select>
          </div>
          
          <div>
            <label class="block text-gray-400 mb-1" htmlFor="search-filter">
              Search
            </label>
            <input
              id="search-filter"
              type="text"
              class="w-full bg-gray-700 text-white rounded p-2 border border-gray-600"
              placeholder="Search bands..."
              value={filters.searchTerm}
              onChange={(e) => handleFilterChange('searchTerm', (e.target as HTMLInputElement).value)}
            />
          </div>
        </div>
      </div>
    </div>
  );
}