import React from 'preact/compat';

interface DayHeaderProps {
  date: string;
  eventCount: number;
  isExpanded: boolean;
  onToggle: (date: string) => void;
}

export function DayHeader({ date, eventCount, isExpanded, onToggle }: DayHeaderProps) {
  const formatDateHeader = (dateStr: string) => {
    const date = new Date(dateStr);
    return date.toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div 
      class="day-separator bg-wgt-black/20 p-2 px-4 rounded-lg cursor-pointer hover:bg-wgt-black/30 transition-colors"
      onClick={() => onToggle(date)}
    >
      <div class="flex items-center justify-between">
        <h2 class="text-xl font-semibold text-wgt-red/75 pb-2">
          {formatDateHeader(date)}
        </h2>
        <div class="flex items-center">
          <span class="text-sm text-gray-400 mr-2">
            {eventCount} event{eventCount !== 1 ? 's' : ''}
          </span>
          <svg 
            class={`w-5 h-5 text-gray-400 transform transition-transform duration-200 ${
              isExpanded ? 'rotate-180' : ''
            }`}
            fill="none" 
            stroke="currentColor" 
            viewBox="0 0 24 24"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
        </div>
      </div>
    </div>
  );
} 