import React, { useState, useEffect } from 'preact/compat';
import { Schedule, EventWithDetails } from '../models/types';
import { loadScheduleData, isScheduleLoaded } from '../utils/apiUtils';
import { getAllEventsWithDetails, sortEventsByDateTime } from '../utils/dataUtils';
import { EventCard } from '../components/EventCard';

export function Home() {
  const [schedule, setSchedule] = useState<Schedule | null>(null);
  const [upcomingEvents, setUpcomingEvents] = useState<EventWithDetails[]>([]);
  const [loading, setLoading] = useState(!isScheduleLoaded());
  const [error, setError] = useState<string | null>(null);
  const [isWelcomeExpanded, setIsWelcomeExpanded] = useState(false);
  
  useEffect(() => {
    // Check if this is the user's first visit
    const hasVisited = localStorage.getItem('wgt-guide-visited');
    if (!hasVisited) {
      setIsWelcomeExpanded(true);
      localStorage.setItem('wgt-guide-visited', 'true');
    }
  }, []);
  
  useEffect(() => {
    async function fetchData() {
      try {
        const data = await loadScheduleData();
        setSchedule(data);
        
        // Get all events with details
        const allEvents = getAllEventsWithDetails(data);
        
        // Sort events by date and time
        const sortedEvents = sortEventsByDateTime(allEvents);
        
        // Get the next 5 upcoming events
        setUpcomingEvents(sortedEvents.slice(0, 5));
      } catch (err) {
        setError('Failed to load schedule data. Please try again later.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
  }, []);

  const toggleWelcome = () => {
    setIsWelcomeExpanded(!isWelcomeExpanded);
  };
  
  if (loading) {
    return (
      <div class="flex items-center justify-center h-64">
        <div class="text-center">
          <div class="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-wgt-red mx-auto"></div>
          <p class="mt-4">Loading festival data...</p>
        </div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div class="bg-red-900 text-white p-4 rounded-lg">
        <h2 class="text-xl font-bold mb-2">Error</h2>
        <p>{error}</p>
      </div>
    );
  }
  
  return (
    <div>
      <section class="mb-10">
        <div class="bg-gradient-to-r from-wgt-purple to-wgt-red rounded-lg text-white mb-6 overflow-hidden">
          {/* Header with toggle button */}
          <div 
            class="p-6 cursor-pointer flex items-center justify-between hover:bg-black hover:bg-opacity-10 transition-colors"
            onClick={toggleWelcome}
          >
            <h1 class="text-xl md:text-2xl font-bold">Welcome to WGT web app</h1>
            <div class="ml-4">
              <svg 
                class={`w-6 h-6 transform transition-transform duration-300 ${isWelcomeExpanded ? 'rotate-180' : ''}`}
                fill="none" 
                stroke="currentColor" 
                viewBox="0 0 24 24"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </div>
          </div>
          
          {/* Collapsible content */}
          <div class={`transition-all duration-300 ease-in-out ${isWelcomeExpanded ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'} overflow-hidden`}>
            <div class="px-6 pb-6">
              <p class="text-lg mb-6">
                Your personal guide to the Wave-Gotik-Treffen festival. Browse the schedule, 
                mark your favorite bands, and create your personalized festival plan.
              </p>
              <div class="flex flex-col sm:flex-row gap-4">
                <a href="/schedule" class="btn btn-primary">
                  View Full Schedule
                </a>
                <a href="/favorites" class="btn btn-secondary">
                  My Favorites
                </a>
              </div>
            </div>
          </div>
        </div>
      </section>
      
      <section class="mb-10">
        <h2 class="text-2xl font-bold mb-4">Upcoming Events</h2>
        {upcomingEvents.length > 0 ? (
          <div>
            {upcomingEvents.map(event => (
              <EventCard key={event.id} event={event} />
            ))}
            <div class="text-center mt-6">
              <a href="/schedule" class="btn btn-primary">
                View All Events
              </a>
            </div>
          </div>
        ) : (
          <p class="text-gray-400">No upcoming events found.</p>
        )}
      </section>
      
      <section>
        <h2 class="text-2xl font-bold mb-4">About Wave-Gotik-Treffen</h2>
        <div class="card">
          <p class="mb-4">
            Wave-Gotik-Treffen (WGT) is one of the largest gothic festivals in the world, 
            held annually in Leipzig, Germany. The festival features a diverse range of 
            music styles including gothic rock, EBM, industrial, noise, darkwave, 
            neofolk, neoclassical, and various other genres.
          </p>
          <p>
            <a 
              href="https://www.wave-gotik-treffen.de/" 
              target="_blank" 
              rel="noopener noreferrer"
              class="text-wgt-red hover:underline"
            >
              Visit the official WGT website →
            </a>
          </p>
        </div>
      </section>
    </div>
  );
}