import React, { useState, useEffect } from 'preact/compat';
import { Link } from 'preact-router/match';
import { Schedule, EventWithDetails } from '../models/types';
import { loadScheduleData, isScheduleLoaded } from '../utils/apiUtils';
import { getAllEventsWithDetails, sortEventsByDateTime, decodeSharedSchedule, addFavorite } from '../utils/dataUtils';
import { EventCard } from '../components/EventCard';

interface SharedScheduleProps {
  schedule?: string;
}

export function SharedSchedule({ schedule: encodedSchedule }: SharedScheduleProps) {
  const [schedule, setSchedule] = useState<Schedule | null>(null);
  const [sharedEvents, setSharedEvents] = useState<EventWithDetails[]>([]);
  const [loading, setLoading] = useState(!isScheduleLoaded());
  const [error, setError] = useState<string | null>(null);
  const [imported, setImported] = useState(false);
  
  useEffect(() => {
    async function fetchData() {
      try {
        if (!encodedSchedule) {
          setError('No schedule data provided');
          setLoading(false);
          return;
        }
        
        // Load the schedule data first
        const data = await loadScheduleData();
        setSchedule(data);
        
        // Now decode the shared schedule (which may contain shortened IDs)
        const eventIds = decodeSharedSchedule(encodedSchedule);
        if (eventIds.length === 0) {
          setError('Invalid schedule data');
          setLoading(false);
          return;
        }
        
        // Get all events with details
        const allEvents = getAllEventsWithDetails(data);
        
        // Filter to only the shared events
        const events = allEvents.filter(event => eventIds.includes(event.id));
        
        // Sort events by date and time
        const sortedEvents = sortEventsByDateTime(events);
        
        setSharedEvents(sortedEvents);
      } catch (err) {
        setError('Failed to load shared schedule. Please try again later.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
  }, [encodedSchedule]);
  
  const handleImportToFavorites = () => {
    sharedEvents.forEach(event => {
      addFavorite(event.id);
    });
    setImported(true);
  };
  
  if (loading) {
    return (
      <div class="flex items-center justify-center h-64">
        <div class="text-center">
          <div class="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-wgt-red mx-auto"></div>
          <p class="mt-4">Loading shared schedule...</p>
        </div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div class="bg-red-900 text-white p-4 rounded-lg">
        <h2 class="text-xl font-bold mb-2">Error</h2>
        <p>{error}</p>
        <a href="/" class="btn btn-primary mt-4">
          Go to Home
        </a>
      </div>
    );
  }
  
  return (
    <div>
      <div class="flex flex-col md:flex-row md:justify-between md:items-center mb-6">
        <h1 class="text-3xl font-bold mb-4 md:mb-0">Shared Festival Schedule</h1>
        
        {sharedEvents.length > 0 && !imported && (
          <button 
            onClick={handleImportToFavorites}
            class="btn btn-primary"
          >
            Import to My Favorites
          </button>
        )}
        
        {imported && (
          <div class="bg-green-800 text-white px-4 py-2 rounded">
            Schedule imported to your favorites!
          </div>
        )}
      </div>
      
      {sharedEvents.length > 0 ? (
        <div>
          {sharedEvents.map(event => (
            <EventCard key={event.id} event={event} showFavoriteButton={false} />
          ))}
          
          <div class="mt-8 flex justify-center">
            <a href="/favorites" class="btn btn-secondary">
              View My Schedule
            </a>
          </div>
        </div>
      ) : (
        <div class="card text-center py-8">
          <p class="text-xl text-gray-400">No events found in the shared schedule.</p>
          <div class="mt-6">
            <a href="/" class="btn btn-primary">
              Go to Home
            </a>
          </div>
        </div>
      )}
    </div>
  );
}