import { EventWithDetails } from '../models/types';

/**
 * Group events by date
 */
export function groupEventsByDate(events: EventWithDetails[]): Record<string, EventWithDetails[]> {
  return events.reduce((acc, event) => {
    const date = event.date;
    if (!acc[date]) {
      acc[date] = [];
    }
    acc[date].push(event);
    return acc;
  }, {} as Record<string, EventWithDetails[]>);
} 