import React from 'preact/compat';
import { useEffect, useState } from 'preact/compat';

function ObfuscatedEmail({ className = "" }: { className?: string }) {
  const [revealed, setRevealed] = useState(false);
  
  const handleClick = () => {
    setRevealed(true);
    // Copy to clipboard for convenience
    navigator.clipboard.writeText('wgtwebapp@gmail.com').catch(() => {
      // Fallback if clipboard API is not available
    });
  };

  if (revealed) {
    return (
      <a 
        href="mailto:wgtwebapp@gmail.com" 
        class={`text-blue-600 hover:text-blue-800 underline ${className}`}
      >
        wgtwebapp@gmail.com
      </a>
    );
  }

  return (
    <button
      onClick={handleClick}
      class={`text-blue-600 hover:text-blue-800 underline cursor-pointer bg-transparent border-none p-0 font-inherit ${className}`}
      title="Click to reveal email address (will also copy to clipboard)"
    >
      wgtwebapp[at]gmail.com
    </button>
  );
}

