import React from "preact/compat";
import { useState } from "preact/compat";

export function Footer() {
  const currentYear = new Date().getFullYear();
  const [showBuildInfo, setShowBuildInfo] = useState(false);
  
  // Get version and build time from injected build variables, fallback for development
  const version = typeof __APP_VERSION__ !== 'undefined' ? __APP_VERSION__ : 'dev';
  const buildTime = typeof __BUILD_TIME__ !== 'undefined' ? __BUILD_TIME__ : new Date().toISOString();
  
  const toggleBuildInfo = () => {
    setShowBuildInfo(!showBuildInfo);
  };
  
  return (
    <footer class="bg-gray-800 py-6">
      <div class="container mx-auto px-4">
        <div class="flex flex-col md:flex-row justify-between items-center">
          <div class="mb-4 md:mb-0">
            <p class="text-gray-400 text-center md:text-left">
              &copy; {currentYear} WGT web app - A Progressive Web App for Wave-Gotik-Treffen
            </p>
          </div>
          <div class="flex flex-col md:flex-row items-center space-y-2 md:space-y-0 md:space-x-4">
            <div class="flex space-x-4">
              <a 
                href="https://www.wave-gotik-treffen.de/" 
                target="_blank" 
                rel="noopener noreferrer"
                class="text-gray-400 hover:text-white transition-colors"
              >
                Official WGT Site
              </a>
              <a 
                href="/impressum"
                class="text-gray-400 hover:text-white transition-colors"
              >
                Impressum
              </a>
            </div>
            
            {/* Version Display */}
            <div class="flex items-center">
              <button
                onClick={toggleBuildInfo}
                class="text-gray-500 hover:text-gray-300 transition-colors text-sm px-2 py-1 rounded hover:bg-gray-700"
                title="Click for build information"
              >
                v{version}
              </button>
              
              {showBuildInfo && (
                <div class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50" onClick={toggleBuildInfo}>
                  <div class="bg-white rounded-lg p-6 m-4 max-w-md" onClick={(e) => e.stopPropagation()}>
                    <h3 class="text-lg font-semibold mb-4 text-gray-800">Build Information</h3>
                    <div class="space-y-2 text-sm text-gray-600">
                      <div class="flex justify-between">
                        <span class="font-medium">Version:</span>
                        <span class="font-mono">{version}</span>
                      </div>
                      <div class="flex justify-between">
                        <span class="font-medium">Build Time:</span>
                        <span class="font-mono text-xs">{new Date(buildTime).toLocaleString()}</span>
                      </div>
                      <div class="flex justify-between">
                        <span class="font-medium">Framework:</span>
                        <span>Preact + Vite</span>
                      </div>
                      <div class="flex justify-between">
                        <span class="font-medium">PWA:</span>
                        <span>Enabled</span>
                      </div>
                    </div>
                    <button
                      onClick={toggleBuildInfo}
                      class="mt-4 w-full bg-gray-800 text-white py-2 px-4 rounded hover:bg-gray-700 transition-colors"
                    >
                      Close
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}