import React, { useState, useEffect } from 'preact/compat';
import { Link } from 'preact-router/match';
import { Schedule, Band, EventWithDetails } from '../models/types';
import { loadScheduleData, isScheduleLoaded } from '../utils/apiUtils';
import { findBandById, findEventsByBandId, sortEventsByDateTime } from '../utils/dataUtils';
import { EventCard } from '../components/EventCard';

interface BandDetailProps {
  id: string;
}

export function BandDetail({ id }: BandDetailProps) {
  const [schedule, setSchedule] = useState<Schedule | null>(null);
  const [band, setBand] = useState<Band | null>(null);
  const [events, setEvents] = useState<EventWithDetails[]>([]);
  const [loading, setLoading] = useState(!isScheduleLoaded());
  const [error, setError] = useState<string | null>(null);
  
  useEffect(() => {
    async function fetchData() {
      try {
        const data = await loadScheduleData();
        setSchedule(data);
        
        // Find the band by ID
        const bandData = findBandById(id, data);
        if (!bandData) {
          setError('Band not found');
          setLoading(false);
          return;
        }
        
        setBand(bandData);
        
        // Find events for this band
        const bandEvents = findEventsByBandId(id, data);
        setEvents(sortEventsByDateTime(bandEvents));
      } catch (err) {
        setError('Failed to load band data. Please try again later.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
  }, [id]);
  
  if (loading) {
    return (
      <div class="flex items-center justify-center h-64">
        <div class="text-center">
          <div class="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-wgt-red mx-auto"></div>
          <p class="mt-4">Loading band information...</p>
        </div>
      </div>
    );
  }
  
  if (error || !band) {
    return (
      <div class="bg-red-900 text-white p-4 rounded-lg">
        <h2 class="text-xl font-bold mb-2">Error</h2>
        <p>{error || 'Band not found'}</p>
        <Link href="/schedule" class="btn btn-primary mt-4">
          Back to Schedule
        </Link>
      </div>
    );
  }
  
  return (
    <div>
      <Link href="/schedule" class="text-gray-400 hover:text-white mb-4 inline-block">
        ← Back to Schedule
      </Link>
      
      <div class="card mb-6">
        <h1 class="text-3xl font-bold mb-2">{band.name}</h1>
        
        {band.genre && (
          <p class="text-gray-400 mb-4">{band.genre}</p>
        )}
        
        {band.description && (
          <div class="mt-4">
            <h2 class="text-xl font-semibold mb-2">About</h2>
            <p class="text-gray-300">{band.description}</p>
          </div>
        )}
      </div>
      
      <div class="mt-8">
        <h2 class="text-2xl font-bold mb-4">Performances</h2>
        
        {events.length > 0 ? (
          <div>
            {events.map(event => (
              <EventCard key={event.id} event={event} />
            ))}
          </div>
        ) : (
          <div class="card text-center py-8">
            <p class="text-xl text-gray-400">No performances scheduled for this band.</p>
          </div>
        )}
      </div>
    </div>
  );
}